$font-stack: Trebuchet, Arial;
$text-color-light: #666;
$text-color-dark: #333;
$green-color: #5db447;
$navigation-bg-color: #fbfbfb;
$content-max-width: 1120px;
$column-1-width: (300px * 100% / $content-max-width);
$column-2-width: (185px * 100% / $content-max-width);
$column-3-width: (120px * 100% / $content-max-width);
$column-4-width: (95px * 100% / $content-max-width);
$column-5-width: (205px * 100% / $content-max-width);
$column-6-width: (130px * 100% / $content-max-width);
$column-7-width: (85px * 100% / $content-max-width);
*, *::before, *::after {
  box-sizing: border-box;
}
body {
  font-family: $font-stack;
  font-size: 16px;
  color: $text-color-light;
}
.wrap {
  width: 100%;
}
.header {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 25px;
  &__top {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    max-width: $content-max-width;
    margin: 0 auto;
  }
  &__logo {
    max-width: 241px;
    width: 100%;
  }
  &__actions {
    display: flex;
    width: auto;
  }
  .header-action {
    &__link {
      color: $text-color-light;
      text-decoration: none;
      margin-left: 15px;
      &:hover {
        color: $text-color-dark;
        cursor: pointer;
      }
    }
    &__phone {
      color: $green-color;
    }
  }
  &__search {
    width: 100%;
    max-width: $content-max-width;
    margin: 0 auto;
    display: flex;
    justify-content: flex-start;
    height: 50px;
    margin-top: 25px;
    .header-form {
      width: 100%;
      display: flex;
      height: 100%;
      border-radius: 0;
      outline: none;
      &__input {
        width: 50%;
        height: 100%;
        border-radius: 0px;
        border: 1px solid $green-color;
        outline: none;
        box-shadow: none;
      }
      &__select {
        width: (50% / 3);
        height: 100%;
        background-color: #eeeeee;
        border: 1px solid $green-color;
        border-radius: 0px;
        -webkit-appearance: none;
        -webkit-border-radius: 0px;
        border-left: none;
        background: url('../img/arrow-down.png') 96% / 15% no-repeat #eee;
        background-size: auto;
        outline: none;
        box-shadow: none;
      }
      &__button {
        width: (50% / 3);
        height: 100%;
        background-color: $green-color;
        border: 1px solid $green-color;
        border-radius: 0;
        outline: none;
        box-shadow: none;
        display: flex;
        align-items: center;
        justify-content: center;
        &:hover {
          color: $text-color-dark;
        }
        i {
          margin-left: 5px;
        }
      }
    }
  }
  &__nav {
    width: 100%;
    height: 32px;
    background: $navigation-bg-color;
    border: 1px solid #e1e1e1;
    margin-top: 10px;
    .header-nav {
      &__item--mobile {
        display: none;
      }
      &__inner {
        display: block;
        width: 100%;
        max-width: $content-max-width;
        margin: 0 auto;
        height: 100%;
      }
      &__menu {
        display: flex;
        align-items: center;
        height: 100%;
      }
      &__item {
        height: 100%;
        padding: 0 10px;
        line-height: 32px;
        .sub-arrow {
          margin-left: 10px;
        }
        a {
          color: $text-color-dark;
        }
        &:hover {
          background: $green-color;
        > a {
            color: white;
            text-decoration: none;
          }
        }
      }
      &__submenu {
        background: #fff;
        border: 1px solid #e1e1e1;
        padding: 13px 0;
      }
      &__subitem {
        font-size: 14px;
        padding: 3px 13px;
        &:hover {
          background: #666;
          > a{
            text-decoration: none;
            color: white;
          }
        }
      }
    }
  }
}
.main {
  width: 100%;
  &__slider-wrap {
    width: 100%;
    background-image: linear-gradient(to right, #fff, $green-color, #fff);
    margin-top: 10px;
  }
  &__slider {
    width: 100%;
    max-width: 960px;
    margin: 0 auto;
    height: 100%;
    .main-slider {
      &__carousel-indicators {
        justify-content: flex-end;
        margin-right: 1rem;
        margin-bottom: 0.3rem;
        li{
          width: 15px;
          height: 15px;
        }
      }
      &__carousel-inner {
        img {
          width: 100%;
        }
      }
    }
  }
  &__cards {
    width: 100%;
    max-width: 960px;
    margin: 10px auto 0;
    display: flex;
    justify-content: space-between;
    .main-cards {
      &__block {
        width: 32%;
        min-width: 300px;
        position: relative;
      }
      &__image {
        width: 100%;
      }
      &__caption {
        position: absolute;
        margin-bottom: 0;
        bottom: 0;
        width: 100%;
        padding: 10px 10px;
        text-align: justify;
        color: white;
        background: #666;
        font-size: 14px;
        min-height: 62px;
      }
    }
  }
  &__center {
    width: 100%;
    max-width: 960px;
    margin: 25px auto 0;
    display: flex;
    justify-content: space-between;
    .main-center {
      &__image {
        width: 100%;
      }
      &__block {
        width: 48%;
        .block-list {
          &__title {
            color: #010000;
            margin: 0;
            font-size: 22px;
            font-weight: bold;
          }
          &__item {
            margin-top: 15px;
          }
          &__topic {
            font-size: 16px;
            color: $text-color-dark;
            margin: 0;
            font-weight: bold;
            line-height: 1.1
          }
          &__text {
            font-size: 16px;
            color: $text-color-light;
            line-height: 1.1;
          }
        }
      }
    }
  }
  &__product {
    width: 100%;
    max-width: $content-max-width;
    margin: 0 auto;
    .product-header {
      display: flex;
      justify-content: space-between;
      &__left {
        background: $green-color;
        height: 32px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        width: 200px;
        letter-spacing: 1px;
        i {
          margin-left: 30px;
          margin-right: -25px;
        }
      }
      &__right {
        display: flex;
        justify-content: flex-end;
      }
      &__select {
        min-width: 200px;
        -webkit-appearance: none;
        -webkit-border-radius: 0px;
        border-radius: 0px;
        border: 1px solid #aaa;
        appearance: none;
        font-size: 14px;
        background: url('../img/arrow-down-grey.png') 96% / 15% no-repeat;
        background-size: auto;
        outline: none;
        box-shadow: none;
        padding: 3px 10px;
        height: 28px;
        line-height: 14px;
        margin-right: 14px;
      }
      &__currency {
        display: flex;
        align-items: center;
        justify-content: flex-end;
      }
      &__link {
        font-size: 14px;
        color: $green-color;
        text-decoration: none;
        line-height: 14px;
        padding: 0 5px;
        &:hover {
          color: #2a8114;
        }
        &--border-right {
          border-right: 1px solid $green-color;
        }
      }
    }
    .product-body {
      padding-bottom: 10px;
      border-bottom: 1px solid $green-color;
      &__head {
        width: 100%;
        display: flex;
        .product-head {
          &__item {
            color: $text-color-light;
            font-size: 14px;
            &--column1 {
              width: $column-1-width;
            }
            &--column2 {
              width: $column-2-width;
            }
            &--column3 {
              width: $column-3-width;
            }
            &--column4 {
              width: $column-4-width;
            }
            &--column5 {
              width: $column-5-width;
            }
            &--column6 {
              width: $column-6-width;
            }
            &--column7 {
              width: $column-7-width;
            }
          }
        }
      }
      &__item {
        display: flex;
        width: 100%;
        border-top: 1px solid $green-color;
        margin-top: 5px;
        padding-top: 25px;
        .product-item {
          &__block {
            text-align: left;
            color: $text-color-dark;
            padding-right: 10px;
            font-size: 14px;
            line-height: 1.2;
            position: relative;
            &--column1 {
              width: $column-1-width;
            }
            &--column2 {
              width: $column-2-width;
            }
            &--column3 {
              width: $column-3-width;
            }
            &--column4 {
              width: $column-4-width;
            }
            &--column5 {
              width: $column-5-width;
            }
            &--column6 {
              width: $column-6-width;
            }
            &--column7 {
              width: $column-7-width;
            }
          }
          &__name {
            font-size: 14px;
            margin-bottom: 10px;
          }
          &__vendor-code {
            font-size: 12px;
            margin-bottom: 0;
          }
          &__supplier--link {
            color: $green-color;
            text-decoration: none;
            font-size: 14px;
            &:hover {
              color: #2a8114;
            }
          }
          &__image {
            width: 100%;
            max-width: 100px;
            cursor: pointer;
          }
          &__caption {
            margin-bottom: 0;
            background: #e7dac2;
            padding: 2px 5px;
            width: 100%;
            max-width: 100px;
            position: absolute;
            bottom: 0;
            cursor: pointer;
            i {
              color: $green-color;
            }
          }
        }
      }
      &__modal {
        display: none;
        position: fixed;
        z-index: 500;
        padding-top: 100px;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        overflow: auto;
        background-color: rgba(0,0,0,0.8);
        /* Modal Content */
        .modal-content {
          position: relative;
          background-color: #dedede;
          margin: auto;
          padding: 0;
          width: 90%;
          max-width: $content-max-width;
        }
        /* The Close Button */
        .close {
          color: white;
          position: absolute;
          top: 10px;
          right: 25px;
          font-size: 35px;
          font-weight: bold;
        }
        .close:hover,
        .close:focus {
          color: #999;
          text-decoration: none;
          cursor: pointer;
        }
        /* Hide the slides by default */
        .mySlides {
          display: none;
          justify-content: center;
          align-items: center;
          img {
            display: block;
            max-width: 490px;
          }
        }
        /* Next & previous buttons */
        .prev,
        .next {
          cursor: pointer;
          position: absolute;
          top: 50%;
          width: auto;
          padding: 16px;
          margin-top: -35px;
          color: $text-color-dark;
          font-weight: bold;
          font-size: 20px;
          transition: 0.6s ease;
          border-radius: 0 3px 3px 0;
          user-select: none;
          -webkit-user-select: none;
        }
        /* Position the "next button" to the right */
        .next {
          right: 0;
          border-radius: 3px 0 0 3px;
        }
        /* On hover, add a black background color with a little bit see-through */
        .prev:hover,
        .next:hover {
          background-color: rgba(0, 0, 0, 0.8);
          color: #ccc;
        }
        /* Number text (1/3 etc) */
        .numbertext {
          color: $text-color-light;
          font-size: 12px;
          padding: 8px 12px;
          position: absolute;
          top: 0;
          left: 10px;
        }
      }
      &__actions {
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-top: 20px;
        .product-actions {
          &__button {
            display: flex;
            min-width: 128px;
            height: 24px;
            background: #ccc;
            padding: 0 5px;
            outline: none;
            box-shadow: none;
          }
          &__text {
            line-height: 24px;
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            font-size: 16px;
            color: black;
            i {
              color: white;
              font-size: 1.5rem;
            }
          }
        }
      }
    }
  }
}
.footer {
  display: flex;
  width: 100%;
  max-width: $content-max-width;
  justify-content: center;
  padding-top: 25px;
  padding-bottom: 15px;
  &__phone {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding-top: 15px;
    margin-right: 25px;
    .footer-phone {
      &__name {
        color: #96a3ad;
        font-size: 18px;
        font-weight: bold;
        letter-spacing: 0.2px;
        line-height: 1.2;
      }
      &__number {
        font-size: 18px;
        color: #344855;
        font-weight: bold;
        letter-spacing: 0.2px;
        line-height: 1.2;
        &-big {
          font-size: 24px;
        }
      }
    }
  }
  &__links {
    display: flex;
    flex-direction: column;
    align-items: center;
    .footer-links {
      &__block {
        display: flex;
        justify-content: space-between;
      }
      &__column {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin-right: 25px;
      }
      &__link {
        color: $text-color-light;
        text-decoration: none;
        &:hover {
          color: $text-color-dark;
        }
      }
      &__copyright {
        margin-top: 20px;
        text-align: center;
      }
    }
  }
}

/* Here start media support for screens less than 900px */
@media screen and (max-width: 899.98px) {
  .header {
    position: relative;
    padding-top: 0;
    &__top {
      order: 1;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    &__logo{
      padding-top: 10px;
    }
    &__search {
      order: 2;
      height: auto;
      .header-form {
        display: flex;
        flex-wrap: wrap;
        &__input {
          width: 100%;
          height: 46px;
        }
        &__select{
          width: 33.33333%;
          height: 46px;
        }
        &__button{
          width: 33.333333%;
        }
      }
    }
    &__nav {
      position: absolute;
      top: 0;
      left: 0;
      max-width: 320px;
      order: 0;
      margin-top: 0;
      background: transparent;
      border: none;
      height: auto;
      .main-menu-btn {
        margin-left: 20px;
        margin-top: 15px;
        z-index: 501;
      }
      .header-nav {
        &__menu {
          flex-direction: column;
          background: #eee;
          align-items: flex-start;
          padding: 20px 0;
        }
        &__item {
          margin-bottom: 10px;
          width: 100%;
          padding: 0 20px;
          font-size: 20px;
          .sub-arrow {
            margin-left: 20px;
          }
        }
        &__item--mobile {
          display: flex;
        }
        &__submenu {
          border: none;
          padding: 20px 0;
          background: transparent;
        }
        &__subitem {
          margin-bottom: 10px;
          font-size: 20px;
          a {
            color: white;
          }
        }
      }
    }
    &__actions {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      align-self: flex-end;
      margin-top: -45px;
      margin-right: 15px;
      .header-action {
        &__registration {
          display: none;
        }
      }
    }
  }
  .main {
    &__slider-wrap {
      margin-top: 25px;
    }
    &-cards {
      padding: 0 15px;
      flex-wrap: wrap;
      margin-top: 25px;
      justify-content: space-around;
      &__block {
        margin-bottom: 25px;
      }
    }
    &__center {
      padding: 0 15px;
      flex-direction: column;
      align-items: center;
      .main-center {
        &__block {
          width: 100%;
          max-width: 600px;
        }
      }
    }
    &__product {
      margin-top: 25px;
      .product-header {
        &__right{
          display: flex;
          flex-direction: column;
          align-items: flex-end;
        }
        &__select {
          margin-bottom: 15px;
        }
        &__currency {
          margin-right: 15px;
        }
      }
      .product-body {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 15px;
        padding: 0 15px 25px;
        border-top: 1px solid $green-color;
        .product-head {
          display: none;
        }
        .product-item {
          display: flex;
          flex-direction: column;
          width: 100%;
          max-width: 600px;
          align-items: center;
          border-top: 0;
          &__block {
            display: flex;
            flex-direction: row;
            align-items: center;
            margin-bottom: 15px;
            &--column1 {
              width: 100%;
            }
            &--column2 {
              width: 100%;
            }
            &--column3 {
              width: 100%;
            }
            &--column4 {
              width: 100%;
            }
            &--column5 {
              width: 100%;
            }
            &--column6 {
              width: 100%;
            }
            &--column7 {
              width: 100%;
            }
          }
          &__head--mobile {
            display: flex;
            align-items: center;
            justify-content: flex-start;
            width: 33%;
            min-width: 100px;
            height: 100%;
          }
          &__content--mobile {
            width: 67%;
          }
        }
        &__actions {
          width: 100%;
          max-width: 600px;
        }
      }
    }
  }
  .footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
    .footer-links {
      margin-top: 25px;
      &__block {
        flex-wrap: wrap;
      }
      &__column {
        width: 45%;
        margin-right: 5%;
        align-items: flex-end;
        &--mobile-right {
          align-items: flex-start;
          margin-right: 0;
          margin-left: 5%;
        }
      }
    }
  }
}
@media screen and (max-width: 489.98px) {
  .header {
    &__logo {
      max-width: 241px;
      width: 60%;
    }
    &__nav {
      max-width: 490px;
    }
    &__actions {
      margin-top: 20px;
      flex-direction: row;
      flex-wrap: wrap;
      width: 100%;
      padding: 0 10px;
      justify-content: space-around;
      margin-right: 0;
      .header-action {
        &__link {
          margin-left: 0;
        }
      }
    }
    &__search {
      .header-form {
        justify-content: center;
      }
      .header-form__select {
        width: 50%;
      }
      .header-form__button {
        width: 50%;
      }
    }
  }
  .main {
    .carousel-indicators li{
      border-bottom: none;
    }
    &__product {
      .product-header {
        flex-wrap: wrap;
        justify-content: space-around;
        &__left {
          margin-bottom: 15px;
        }
        &__select {
          margin-right: 0;
        }
        &__currency {
          align-self: center;
        }
      }
      .product-body__modal {
        .mySlides img {
          width: 100%;
        }
      }
    }
  }
}
